import { Component, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { VehicleService } from '../business/services/vehicle.service'

@Component({
  selector: 'app-vehicle-report',
  templateUrl: './vehicle-report.component.html',
  styleUrls: ['./vehicle-report.component.scss'],
})
export class VehicleReportComponent implements OnInit {
  constructor(private route: ActivatedRoute, private vehicleService: VehicleService) {}

  ngOnInit() {
    this.route.queryParams.subscribe((params) => {
      if (params.reportId) {
        try {
          //console.log(JSON.parse(atob(params.reportId)))
          const { invoiceId, vehicleId }: { invoiceId: string; vehicleId: string } = JSON.parse(atob(params.reportId))
          if (!invoiceId || !vehicleId) throw new Error('Invalid report id')

          //get report
          this.vehicleService.getReportPdf(invoiceId, vehicleId)
        } catch (error) {
          this.onError()
        }
      } else {
        this.onError()
      }
    })
  }

  onError() {
    alert('Invalid report id')
  }
}
