import { Pipe, PipeTransform } from "@angular/core";
import { City } from "../business/models/geo.models";

//return string with name og city and state code

@Pipe({
  name: "location",
})
export class LocationPipe implements PipeTransform {
  transform(value: City, ...args: unknown[]): string {
    return `${value?.name}, ${value?.state_code}`;
  }
}
