import { Component, OnDestroy, OnInit } from "@angular/core";
import { Subscription } from "rxjs";
import { CompanyService } from "src/app/business/services/company.service";
import { GlobalStateService } from "src/app/business/state/global-state.service";

@Component({
  selector: "app-main-menu",
  templateUrl: "./main-menu.component.html",
  styleUrls: ["./styles/main-menu.component.scss"],
})
export class MainMenuComponent implements OnInit, OnDestroy {
  subs: Subscription[] = [];
  companyName: string = "Loading...";

  constructor(private companyService: CompanyService, private globalState: GlobalStateService) {
    const sub = this.globalState.userCompany$.subscribe((company) => {
      if(company){
        this.companyName = company.name;
      }
    });
    this.subs.push(sub);
  }

  ngOnInit() {
    this.companyService.fetchCompany();
  }

  ngOnDestroy() {
    this.subs.forEach((sub) => sub.unsubscribe());
  }
}
