import { Injectable } from "@angular/core";
import { HttpInterceptor, HttpRequest, HttpResponse, HttpHandler, HttpEvent, HttpErrorResponse } from "@angular/common/http";
import { AuthenticationService } from "src/app/authentication/authentication.service";
import { Observable, throwError } from "rxjs";
import { map, catchError } from "rxjs/operators";
import { Router } from "@angular/router";

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
  constructor(private authService: AuthenticationService, private router: Router) {}

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (this.authService.isLoggedIn()) {
      let access_token = this.authService.getAccessToken();
      request = request.clone({ headers: request.headers.set("Authorization", "Bearer " + access_token) });
    }

    return next.handle(request).pipe(
      map((event: HttpEvent<any>) => {
        if (event instanceof HttpResponse) {
          //console.log("Network event--->>>", event); //TODO logger with log levels
        }
        return event;
      }),
      catchError((error: HttpErrorResponse) => {
        const data = {
          reason: error?.error?.reason ?? "",
          status: error?.status,
        };
        // console.log("Network event--->>> Error: ", data); //TODO logger with log levels
        // console.log(error);
        
        if (error.statusText == "Unauthorized" || error.statusText == "Forbidden") {
          this.authService.logout().subscribe(() => this.router.navigate(["auth/signin"]));
        }

        return throwError(error);
      })
    );
  }
}
