import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl, FormBuilder, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { ValidatorsService } from "src/app/core/validators.service";
import { AuthenticationService } from "../authentication.service";

@Component({
  selector: "app-reset-password",
  templateUrl: "./reset-password.component.html",
  styleUrls: ["./reset-password.component.scss"],
})
export class ResetPasswordComponent implements OnInit {
  recoverPasswordForm: FormGroup;
  token: string;
  loading = false;
  error = false;
  notEqualPass = false;
  passwordSaved = false;

  constructor(
    public router: Router,
    private authenticationService: AuthenticationService,
    private fb: FormBuilder,
    private validatorsService: ValidatorsService,
    private route: ActivatedRoute
  ) {}
  ngOnInit() {
    this.route.queryParams.subscribe((params) => {
      if (params.token) {
        this.token = params.token;
      }else{
        return this.router.navigate(["/auth/signin"]);
      }
    });

    this.recoverPasswordForm = this.fb.group(
      {
        password: ["", [Validators.required, Validators.minLength(8), Validators.maxLength(20)]],
        confirm_password: ["", [Validators.required, Validators.minLength(8), Validators.maxLength(20)]],
      },
      {
        validators: this.validatorsService.equalPasswords("password", "confirm_password"),
      }
    );
  }

  onRecoverPassword() {
    if (this.recoverPasswordForm.controls?.confirm_password?.errors?.notEqual) {
      this.notEqualPass = true;
    } else {
      this.notEqualPass = false;
    }
    if (this.recoverPasswordForm.invalid) {
      return Object.values(this.recoverPasswordForm.controls).forEach((control) => {
        if (control instanceof FormGroup) {
          Object.values(control.controls).forEach((control) => control.markAsTouched());
        } else {
          control.markAsTouched();
        }
      });
    } else {
      this.loading = true;
      this.authenticationService.resetPassword(this.recoverPasswordForm.value.password, this.token).subscribe(
        (res) => {
          this.passwordSaved = true;
          this.loading = false;
          setTimeout(() => {
            return this.router.navigate(["/auth/signin"]);
          }, 3000);
        },
        (err) => {
          this.error = true;
          this.passwordSaved = false;
          this.loading = false;
        }
      );
    }
  }
}
