import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { Invoice } from "../models/invoice.models";
import { StateService } from "../../core/state.service";
import { Vehicle } from "../models/vehicle.models";
import { Rate } from "../models/rate.models";
import { nanoid } from "nanoid";
import { Customer } from "../models/customer.models";

interface InvoicesState {
  invoices: Invoice[];
  newInvoice: Partial<Invoice>; //new invoice
  selectedInvoice: Invoice; //invoice to edit
  curentRate: Rate; //new invoice current rate
  selectedInvoices: Invoice[]; //invoices checked on table
  mostRecentsCustomers: Customer[];
}

const initialState: InvoicesState = {
  invoices: [],
  newInvoice: { status: "DRAFT", vehicles: [] },
  selectedInvoice: null,
  curentRate: null,
  selectedInvoices: [],
  mostRecentsCustomers: [],
};

@Injectable({
  providedIn: "root",
})
export class InvoicesStateService extends StateService<InvoicesState> {
  invoices$: Observable<Invoice[]> = this.select((state) => state.invoices);
  newInvoice$: Observable<Partial<Invoice>> = this.select((state) => state.newInvoice);
  selectedInvoice$: Observable<Partial<Invoice>> = this.select((state) => state.selectedInvoice);
  currentRate$: Observable<Rate> = this.select((state) => state.curentRate);
  selectedInvoices$: Observable<Invoice[]> = this.select((state) => state.selectedInvoices);
  mostRecentsCustomers$: Observable<Customer[]> = this.select((state) => state.mostRecentsCustomers);

  constructor() {
    super(initialState);
  }

  setInvoices(invoices: Invoice[]) {
    this.setState({ invoices });
  }

  setNewInvoice(invoice: Partial<Invoice>) {
    this.setState({ newInvoice: { ...this.state.newInvoice, ...invoice } });
  }

  setSelectedInvoice(invoice: Partial<Invoice>) {
    this.setState({ selectedInvoice: { ...this.state.selectedInvoice, ...invoice } });
  }

  addNewInvoiceVehicle(vehicle: Vehicle) {
    const vehicles = this.state.newInvoice?.vehicles ?? [];
    if (vehicles.find((v) => v.vin === vehicle.vin)) {
      throw new Error("Duplicated vin");
    }
    vehicles.push({ ...vehicle, _id: nanoid(10) }); //temporal id until saved on db
    this.setState({ newInvoice: { ...this.state.newInvoice, vehicles } });
  }

  addSelectedInvoiceVehicle(vehicle: Vehicle) {
    const vehicles = this.state.selectedInvoice?.vehicles ?? [];
    if (vehicles.find((v) => v.vin === vehicle.vin)) {
      throw new Error("Duplicated vin");
    }
    vehicles.push({ ...vehicle, _id: nanoid(10) });
    this.setState({ selectedInvoice: { ...this.state.selectedInvoice, vehicles } });
  }

  editNewInvoiceVehicle(vehicle: Vehicle, id: string) {
    const vehicles = this.state.newInvoice?.vehicles ?? [];
    if (vehicles.find((v) => v.vin === vehicle.vin && v._id !== id)) {
      throw new Error("Duplicated vin");
    }
    const newVehicles = vehicles.map((v) => {
      if (v._id === id) {
        return vehicle;
      } else {
        return v;
      }
    });

    this.setState({ newInvoice: { ...this.state.newInvoice, vehicles: newVehicles } });
  }

  editSelectedInvoiceVehicle(vehicle: Vehicle, id: string) {
    const vehicles = this.state.selectedInvoice?.vehicles ?? [];
    if (vehicles.find((v) => v.vin === vehicle.vin && v._id !== id)) {
      throw new Error("Duplicated vin");
    }
    const newVehicles = vehicles.map((v) => {
      if (v._id === id) {
        return vehicle;
      } else {
        return v;
      }
    });

    this.setState({ selectedInvoice: { ...this.state.selectedInvoice, vehicles: newVehicles } });
  }

  deleteNewInvoiceVehicle(vehicleId: string) {
    const newVehicles = this.state.newInvoice?.vehicles?.filter((v) => v._id !== vehicleId);
    this.setState({ newInvoice: { ...this.state.newInvoice, vehicles: newVehicles } });
  }

  deleteSelectedInvoiceVehicle(vehicleId: string) {
    const newVehicles = this.state.selectedInvoice?.vehicles?.filter((v) => v._id !== vehicleId);
    this.setState({ selectedInvoice: { ...this.state.selectedInvoice, vehicles: newVehicles } });
  }

  resetNewInvoice() {
    this.setState({ newInvoice: { status: "DRAFT", vehicles: [] } });
  }

  resetSelectedInvoice() {
    this.setState({ selectedInvoice: null });
  }

  setCurretRate(rate: Rate) {
    this.setState({ curentRate: rate });
  }

  setSelectedInvoices(invoice: Invoice, selected: boolean) {
    if (selected) {
      this.setState({ selectedInvoices: [...this.state.selectedInvoices, invoice] });
    } else {
      this.setState({ selectedInvoices: this.state.selectedInvoices.filter((inv) => inv.id !== invoice.id) });
    }
  }

  setAllSelectedInvoices(invoices: Invoice[], selected: boolean) {
    if (selected) {
      this.setState({ selectedInvoices: invoices });
    } else {
      this.setState({ selectedInvoices: [] });
    }
  }

  setMostRecentsCustomers(customers: Customer[]) {
    this.setState({ mostRecentsCustomers: customers });
  }
}
