import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { format } from "date-fns/esm";
import { BehaviorSubject } from "rxjs";
import { map } from "rxjs/operators";
import { environment } from "src/environments/environment";
import { Company, CompanyBody, DashboardInfo, Report } from "../models/company.models";
import { Invoice } from "../models/invoice.models";
import { User } from "../models/user.models";
import { GlobalStateService } from "../state/global-state.service";

@Injectable({
  providedIn: "root",
})
export class CompanyService {
  private baseUrl = environment.apiUrl;

  public company: BehaviorSubject<Company> = new BehaviorSubject(null);
  public dashboardInfo: BehaviorSubject<DashboardInfo> = new BehaviorSubject(null);

  constructor(private http: HttpClient, private globalState: GlobalStateService) {}

  fetchCompany() {
    this.http.get<Company>(`${this.baseUrl}/company/admin`).subscribe((data) => {
      this.company.next(data);
      this.globalState.setUserCompany(data);
    });
  }

  fetchDashboardInfo(time: "week" | "month") {
    this.http.get<DashboardInfo>(`${this.baseUrl}/company/admin/dashboardInfo?time=${time}`).subscribe((data) => {
      this.dashboardInfo.next(data);
    });
  }

  getReport(driverId: string, dateFrom: string, dateTo: string) {
    return this.http.get<Report>(`${this.baseUrl}/company/admin/report/${driverId}?dateFrom=${dateFrom}&dateTo=${dateTo}`);
  }

  getReportPdf(driver: User, dateFrom: string, dateTo: string) {
    const realDateTo = new Date(dateTo).getTime() > Date.now() ? Date.now() : new Date(dateTo)
    const name = `${driver.first_name}_${driver.last_name}_${format(new Date(dateFrom), 'MM-dd-yyyy')}_${format(
      new Date(realDateTo),
      "MM-dd-yyyy"
    )}`;
    this.http
      .post(
        `${this.baseUrl}/company/admin/report/${driver.id}/pdf?dateFrom=${dateFrom}&dateTo=${dateTo}`,
        {},
        {
          responseType: "arraybuffer",
        }
      )
      .subscribe((data: any) => {
        const blob = new Blob([data], { type: "application/pdf" });
        const url = window.URL.createObjectURL(blob);
        //window.open(url);
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", name + ".pdf");
        document.body.appendChild(link);
        link.click();
      });
  }

  search(body: any) {
    return this.http.post<Invoice[]>(`${this.baseUrl}/company/admin/search`, body);
  }

  updateCompany(body: CompanyBody) {
    return this.http.put<Company>(`${this.baseUrl}/company/admin`, body).pipe(
      map((res) => {
        this.fetchCompany();
        return res;
      })
    );
  }

  updateInvoiceNumber(body: { starting_invoice_number: string }) {
    return this.http.put<Company>(`${this.baseUrl}/company/admin/startInvoiceNumber`, body).pipe(
      map((res) => {
        this.fetchCompany();
        return res;
      })
    );
  }
}
