import { I } from "@angular/cdk/keycodes";
import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl, FormBuilder, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { AuthenticationService } from "../authentication.service";

@Component({
  selector: "app-signin",
  templateUrl: "./signin.component.html",
  styleUrls: ["./signin.component.scss"],
})
export class SigninComponent implements OnInit {
  signinForm: FormGroup;
  error: string | null = null;

  constructor(public router: Router, private authenticationService: AuthenticationService, private fb: FormBuilder) {}

  ngOnInit() {
    this.signinForm = this.fb.group({
      email: ["", [Validators.required, Validators.email]],
      password: ["", [Validators.required]],
    });
  }

  onSignin() {
    if (this.signinForm.invalid) {
      return Object.values(this.signinForm.controls).forEach((control) => {
        if (control instanceof FormGroup) {
          Object.values(control.controls).forEach((control) => control.markAsTouched());
        } else {
          control.markAsTouched();
        }
      });
    } else {
      this.error = null;

      const data = this.signinForm.value;

      this.authenticationService.signin(data.email, data.password).subscribe(
        (res) => {
          if (!res.user?.roles?.includes("ADMIN") && !res.user?.roles?.includes("MANAGER")) {
            this.error = "Forbidden access";
            return;
          }
          // if signin success then:
          this.router.navigate(["general"]);
        },
        (err) => {
          // else if signin fails
          if (err.error?.code === 401) {
            this.error = err.error.message;
          } else {
            this.error = "Unexpected error...";
          }
        }
      );
    }
  }
}
