import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl } from "@angular/forms";
import { Router } from "@angular/router";
import { AuthenticationService } from "../authentication.service";

@Component({
  selector: "app-forgot-password",
  templateUrl: "./forgot-password.component.html",
  styleUrls: ["./forgot-password.component.scss"],
})
export class ForgotPasswordComponent implements OnInit {
  recoverPasswordForm: FormGroup;
  emailSubmitted = false;
  invalidEmail = false;
  loading = false;

  constructor(public router: Router, private authenticationService: AuthenticationService) {}

  ngOnInit() {
    this.recoverPasswordForm = new FormGroup({
      email: new FormControl(""),
    });
  }

  onRecoverPassword() {
    this.loading = true;
    this.authenticationService.recoverPassword(this.recoverPasswordForm.value.email).subscribe(
      (res) => {
        // if recoverpassword success then:
        this.emailSubmitted = true;
        this.invalidEmail = false;
        this.loading = false;
        setTimeout(() => {
          return this.router.navigate(["/auth/signin"]);
        }, 3000);
      },
      (err) => {
        console.log(err);
        this.emailSubmitted = false;
        this.invalidEmail = true;
        this.loading = false;

        // else if recoverpassword fails
        // show error
      }
    );
  }
}
