import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { map } from "rxjs/operators";
import { environment } from "src/environments/environment";
import { VinData } from "../models/vehicle.models";

@Injectable({
  providedIn: "root",
})
export class VehicleService {
  private baseUrl = environment.apiUrl;
  private timerId: any;

  years: number[] = [];
  makes: string[] = MAKES;

  constructor(private http: HttpClient) {
    this.setYears();
  }

  searchVin(vin: string): Promise<VinData> {
    const wait = 600;
    clearTimeout(this.timerId);

    return new Promise((resolve, reject) => {
      this.timerId = setTimeout(() => {
        this.http
          .get<VinData>(`${this.baseUrl}/general/vin/${vin}`)
          .pipe(
            map((data) => {
              if (!data.valid) {
                throw new Error("Invalid vin");
              }
              return data;
            })
          )
          .subscribe(
            (data) => resolve(data),
            (err) => reject(err)
          );
      }, wait);
    });
  }

  getModelsByMake(make: string): Promise<string[]> {
    return new Promise((resolve, reject) => {
      this.http.get<string[]>(`${this.baseUrl}/general/vehicleModels/${make}`).subscribe(
        (data) => resolve(data),
        (err) => reject(err)
      );
    });
  }

  setYears() {
    const currentYear = new Date().getFullYear();
    for (let i = currentYear; i >= 1950; i--) {
      this.years.push(i);
    }
  }


  getReportPdf(invoiceId: string, vehicleId: string) {
    this.http
      .get(
        `${this.baseUrl}/general/vehicleReport/${invoiceId}/${vehicleId}`,
        {
          responseType: "arraybuffer",
        }
      )
      .subscribe((data: any) => {
        const blob = new Blob([data], { type: "application/pdf" });
        const url = window.URL.createObjectURL(blob);
        location.replace(url)
        // const link = document.createElement("a");
        // link.href = url;
        // link.setAttribute("download", name + ".pdf");
        // document.body.appendChild(link);
        // link.click();
      });
  }
}

